/* CSS Document */
/* =====  ===== */
.header {
  position: relative;
  width: 100%;
  z-index: 200;
}
.header-logo {
  position: absolute;
  top: 45px;
  left: 60px;
  width: 307px;
  height: 26px;
  margin-top: -16px;
  background: url(/static/img/logo.0aed3eb.png) no-repeat;
  background-size: 307px 26px;
  cursor: pointer;
  z-index: 300;
}
.header-nav-list {
  position: relative;
  padding-left: 480px;
  z-index: 300;
}
.header-nav-item {
  position: relative;
  display: inline-block;
  margin-right: 80px;
  color: #fff;
}
.header-nav-item>a{
  font-size: 16px;
  line-height: 90px;
  display: inline-block;
  color: inherit;
  opacity: .8;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.header-nav-item.active>a{
  opacity: 1;
}
.header-nav-item:hover>a {
  color: #177fff;
}
.header-nav-item.header-user{
  position: absolute;
  right: 0;
  top: 0;
  z-index: 300;
}
.header-bg-toggle {
  position: absolute;
  left: 0;
  top: -90px;
  width: 100%;
  height: 90px;
  background-color: rgba(2, 7, 23, .95);
  -webkit-transition: all .3s ease-out;
  transition: all .3s ease-out;
  -webkit-transition-timing-function: cubic-bezier(.2, 1, .3, 1);
          transition-timing-function: cubic-bezier(.2, 1, .3, 1);
  z-index: 200;
  opacity: 0;
}
.subnav{
  position: absolute;
  left: 0;
  top: 90px;
  z-index: 201;
}
.subnav-light {
  position: absolute;
  width: 270px;
  height: 20px;
  background: url(data:image/png;base64,) no-repeat 50%;
  top: 70px;
  left: '50%';
  margin-left: -135px;
  -webkit-transition: all .2s ease-out;
  transition: all .2s ease-out;
  z-index: 201;
  opacity: 0;
}
.subnav-line{
  position: absolute;
  left: '50%';
  top: 70px;
  height: 2px;
  background: #177fff;
  -webkit-transition: all .2s ease-out;
  transition: all .2s ease-out;
  z-index: 201;
  opacity: 0;
}
.subnav-title {
  color: #fff;
  font-size: 16px;
  margin-top: 20px;
}
.subnav-list {
  margin-top: 5px;
  margin-bottom: 20px;
}
.subnav-list li {
  line-height: 30px;
  color: #fff;
  opacity: .8;
  cursor: pointer;
}
.subnav-list li a {
  color: inherit;
  display: block;
  /*transition: all .3s ease;*/
}
.subnav-list li a:hover {
  color: #177fff;
}
